import { GetComponentProps, styled } from 'topwrite';
import { Children, MouseEvent, useState } from 'react';
import copy from 'copy-to-clipboard';

function getChildrenToText(children: any): string {
    return Children.map(children, child => {
        if (typeof child === 'string') {
            return child;
        } else {
            return child.props.children ?
                getChildrenToText(child.props.children) : '';
        }
    }).join('');
}

export default function Code({ children, props }: GetComponentProps<'html:pre'>) {

    const [copied, setCopied] = useState(false);

    const onClick = (e: MouseEvent) => {
        e.preventDefault();
        e.stopPropagation();
        const text = getChildrenToText(props.children);

        copy(text);

        setCopied(true);

        setTimeout(() => {
            setCopied(false);
        }, 1000);
    };

    return <Container>
        {children}
        <Copy onClick={onClick}>
            {copied ? '已复制' : '复制'}
        </Copy>
    </Container>;
}

const Copy = styled.span`
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 3px 6px;
  margin: 0px;
  text-transform: uppercase;
  border-radius: 3px;
  line-height: 1em;
  font-size: 12px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
  display: none;

  &:hover {
    border-color: rgba(0, 0, 0, 0.2);
  }
`;

const Container = styled.div`
  position: relative;

  &:hover {
    ${Copy} {
      display: block;
    }
  }
`;
